.header {
  padding: 12px 0 10px;
  position: fixed;
  z-index: 10;
  background-color: #ffffff;
  width: 100%;
  box-shadow: 0 10px 15px 0 rgba(0, 0, 0, 0.2);
}

.header__container {
  display: flex;
  align-items: center;
  padding: 0 39px 0 46px;
}
@media (max-width: 1279px) {
  .header__container {
    justify-content: flex-start;
  }
}

.header__logo {
  margin-right: 6%;
}
.header__logo img {
  max-width: 121px;
}

.nav {
  width: 100%;
  display: flex;
}
@media (max-width: 1180px) {
  .nav {
    justify-content: end;
  }
}

.nav__panel {
  display: flex;
  flex-direction: row;
  width: 100%;
}
@media (max-width: 1180px) {
  .nav__panel {
    display: none;
  }
  .nav__panel.nav__panel--visible {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: 0;
    top: 100%;
    right: 0;
    bottom: 0;
    background-color: #ffffff;
    z-index: 20;
    width: 100%;
    height: 500px;
    overflow: hidden;
    overflow-y: auto;
  }
}

.nav__toggle {
  order: 1;
  margin-left: 20px;
  border: 0;
  padding: 0;
  width: 32px;
  height: 32px;
  background-color: transparent;
  position: relative;
  cursor: pointer;
}
.nav__toggle::before, .nav__toggle::after,
.nav__toggle span {
  content: "";
  position: absolute;
  left: 0;
  width: 32px;
  height: 2px;
  background-color: #000;
  transition: transform 0.3s ease, top 0.3s ease, opacity 0.3s ease;
}
.nav__toggle::before {
  top: 8px;
}
.nav__toggle span {
  top: 15px;
}
.nav__toggle::after {
  top: 22px;
}
.nav__toggle--opened::before {
  top: 15px;
  transform: rotate(45deg);
}
.nav__toggle--opened span {
  opacity: 0;
}
.nav__toggle--opened::after {
  top: 15px;
  transform: rotate(-45deg);
}

.nav__list {
  display: flex;
  gap: 28px;
  padding: 0;
  margin: 0;
  list-style-type: none;
}
@media (max-width: 1180px) {
  .nav__list {
    flex-direction: column;
    margin: 0 auto 30px;
  }
}

.nav__link {
  text-decoration: none;
  font-weight: 400;
  font-size: 18px;
  color: #4d4d4d;
  position: relative;
}
@media (max-width: 1180px) {
  .nav__link {
    font-size: 30px;
    line-height: 43px;
  }
}
.nav__link:hover {
  color: #000000;
}
.nav__link:hover::after {
  content: "";
  width: 100%;
  position: absolute;
  height: 2px;
  display: block;
  background-color: #000000;
}

.nav__phone {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  gap: 6px;
  margin-left: auto;
  margin-right: 37px;
  position: relative;
}
@media (max-width: 1180px) {
  .nav__phone {
    margin: 0 auto 20px;
  }
}

.nav__phone a {
  font-size: 18px;
  line-height: 24px;
  color: #4d4d4d;
  font-weight: 900;
  font-family: "Roboto";
  text-decoration: none;
}
.nav__phone a:hover {
  color: #000000;
}

@media (min-width: 1181px) {
  .nav__toggle {
    display: none;
  }
}

